@font-face {
  font-family: 'San Francisco';
  src: url('./fonts/san_francisco-webfont.woff') format('woff'),
    url('./fonts/san_francisco-webfont.woff2') format('woff2');
}

body {
  margin: 0;
  font-family: "San Francisco";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --gray: #dbdbdb;
  --dark-gray: #434343;
  --purple-2: #935dff;
  --purple-1: #5e17eb;
  --blue-2: #5271ff;
  --seffaf: #ffffff4f;
  background-image: url("./images/bg.png");
  background-position: center;
  /* background-image: linear-gradient(45deg, #38b6ff 10%, #38b6ff 10%, #5271ff 10%, #5271ff 20%, #5e17eb 20%, #5e17eb 30%, #935dff 30%, #935dff 40%, #dbdbdb 40%, #dbdbdb 50%, #38b6ff 50%, #38b6ff 60%, #5271ff 60%, #5271ff 70%, #5e17eb 70%, #5e17eb 80%, #935dff 80%, #935dff 90%, #dbdbdb 90%, #dbdbdb 100%); */
  background-attachment: fixed;
}

.App {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
}

.main-component {
  position: relative;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  display: inline-block;
  justify-content: center;
  align-items: center;
}

.header-component {
  box-sizing: border-box;
  height: 80px;
  width: 90%;
  margin: 1rem;
  background-color: var(--dark-gray);
  -webkit-border-radius: 1200px;
  -moz-border-radius: 1200px;
  border-radius: 1200px;
  font-family: 'San Francisco';
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  display: inline-block;
  vertical-align: middle;
  justify-content: center;
  position: sticky;
  top: 1rem;
  z-index: 1;
}

.button-tr {
  --btn-color: transparent;
  --btn-fg: 54, 54, 54;
  --btn-border-color: transparent;
  --btn-shadow: 0 0 0 0.2rem rgba(64, 88, 113, 0.5);
  --btn-hover-color: var(--dark-gray);
  color: var(--orange);
  padding: 0px;
  margin: 0.9rem;
  font-weight: normal;
  text-transform: capitalize;
  height: 100%;
}

.buttons {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100%;
  float: right;
  margin-right: 20px;
}

.team-component {
  box-sizing: border-box;
  width: 100%;
  margin-top: 1rem;
  display: inline-block;
  vertical-align: middle;
  justify-content: center;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  border-radius: 100px;
}

.others-component {
  box-sizing: border-box;
  width: 100%;
  display: inline-block;
  vertical-align: middle;
  justify-content: center;
}

.team-inst {
  align-items: center;
  display: flex;
  justify-content: center;
}

.team-studs {
  align-items: center;
  display: flex;
  justify-content: center;
}

.team-card {
  background-color: var(--dark-gray);
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  width: 20%;
  margin: 1rem 1rem 1rem 1rem;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  color: var(--gray);
}

/* .team-card:hover {
  background-color:var(--blue-1);
  color: var(--dark-gray);
  transition: 0.5s ease-in;
} */

.card-link {
  align-items: center;
  justify-content: center;
  float: left;
  width: 40px;
  height: 40px;
  position: absolute;
}

.card-a {
  color: inherit;
}

.card-a:hover {
  color: inherit;
  transition: 0.5s ease-in;
}

.card-name {
  margin-top: 1rem;
  font: 'San Francisco';
  font-size: large;
  font-weight: normal;
  color: inherit;
}

.card-lastname {
  margin-bottom: 1rem;
  font: 'San Francisco';
  font-size: large;
  font-weight: normal;
  color: inherit;
}

.card-job {
  margin-bottom: 1rem;
  font: 'San Francisco';
  font-weight: light;
  font-size: small;
  color: inherit;
}

.divider-component {
  box-sizing: border-box;
  width: 80%;
  display: inline-block;
  vertical-align: middle;
  justify-content: center;
  margin-top: 3rem;
  margin-left: 2rem;
  margin-right: 2rem;
}

.divider-content {
  align-items: center;
  display: flex;
  justify-content: center;
  color: var(--gray);
  font: 'San Francisco';
  font-weight: normal;
  font-size: 40px;
  box-sizing: border-box;
  margin: 0;
  text-shadow: 4px 4px rgba(0, 0, 0, 0.25);
}

.divider {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0;
  border-top: 4rem solid rgb(173, 181, 189);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.tech-component {
  box-sizing: border-box;
  width: 100%;
  display: inline-block;
  vertical-align: middle;
  justify-content: center;
  margin-bottom: 5rem;
}

.tech-packet {
  background-color: var(--dark-gray);
  -webkit-border-radius: 1200px;
  -moz-border-radius: 1200px;
  border-radius: 1200px;
  display: inline-block;
  vertical-align: middle;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  padding: 20px;
  width: 75%;
  margin-top: 1rem;
}

.tech {
  display: inline-block;
  vertical-align: middle;
  justify-content: center;
  margin: 2rem;
  padding: 20px;
}

.tech-image {
  display: inline-block;
  vertical-align: middle;
  justify-content: center;
  width: 80px;
  height: 80px;
  margin: 1rem 2.3rem;
}

.logos {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100%;
  float: left;
  width: 40%;
}

.logo-1 {
  width: 13%;
  height: 100%;
  vertical-align: middle;
  align-items: center;
  display: flex;
  justify-content: center;
  margin-left: 2rem;
}

.logo-2 {
  width: 70%;
  height: 100%;
  vertical-align: middle;
  align-items: center;
  display: flex;
  justify-content: center;
}

.footer-component {
  box-sizing: border-box;
  height: 60px;
  width: 100%;
  background-color: var(--dark-gray);
  vertical-align: middle;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}

.footer-content {
  vertical-align: middle;
  justify-content: center;
  align-items: center;
  display: flex;
  height: 100%;
  color: var(--gray);
  font: 'San Francisco';
  font-weight: normal;
}

.about-component {
  width: 90%;
  align-items: center;
  display: flex;
  margin: 5rem;
  margin-bottom: 9rem;
  padding: 0 15rem;
}

.about-box {
  width: 100%;
  align-items: center;
  display: flex;
}

.about-video {
  /* background-color: var(--dark-gray); */
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.boxer-l {
  background-color: var(--seffaf);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;

  width: 50%;
  padding: 30px;
  margin: 0px;
  
  text-align: left;
  font-size: 14px;
  color: var(--dark-gray);

  float: left;
  /* animation: 1s ease-out 0s 1 slideInFromLeft; */
}

.boxer-r {
  background-color: var(--seffaf);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;

  width: 50%;
  padding: 30px;
  margin: 0px;
  
  text-align: left;
  font-size: 14px;
  color: var(--dark-gray);

  float: right;
  /* animation: 1s ease-out 0s 1 slideInFromLeft; */
}

.my-paragraph-1 {
  font: 'San Francisco';
  font-size: 14px;
  justify-content: center;
  font-weight: light;
}

.my-header-1 {
  font: 'San Francisco';
  font-weight: normal;
  font-size: 32px;
  text-align: center;
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.25);
}

.watch-component {
  background-color: var(--seffaf);
  height: 900px;
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: 5rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.watch-box {
  width: 100%;
  box-sizing: border-box;
}

.watch-header {
  width: 100%;
  font-size: xx-large;
  align-items: center;
  justify-content: center;
  vertical-align: center;
  color: var(--dark-gray);
  font: 'San Francisco';
  font-weight: normal;
  font-size: 40px;
  box-sizing: border-box;
}

.watch-par {
  margin: 0 3rem;
  color: var(--dark-gray);
  font: 'San Francisco';
  font-weight: normal;
  font-size: 14px;
  align-items: center;
  justify-content: center;
  vertical-align: center;
}

.download-component {
  width: 100%;
  margin-top: 1rem;
  align-items: center;
  justify-content: center;
  vertical-align: center;
}

.download-box {
  color: black;
  font-size: 24px;
  align-items: center;
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 755px) {

  .buttons {
    display: none;
  }

  .header-component {
    align-items: center;
    display: flex;
    justify-content: center;
  }

  .logo-1 {
    margin: 1rem;
  }
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes slideInFromBottom {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}